<template>
  <div id="TurnsRegister">
    <button
      @click="add()"
      style="background-color: #1d425e; color: white"
      class="btn mb-3"
    >
      Agregar Turno
    </button>
    <div>
      <b-modal scrollable centered v-model="show" title="Agregar Turno">
        <b-container fluid>
          <form>
            <AlertError ref="error" />
            <div class="form-group">
              <label for="exampleInputEmail1">Seleccionar Turno</label>
              <b-form-select
                class="form-control"
                v-model="data.idTwo"
                :options="turnos"
                required
              ></b-form-select>
            </div>
            <div class="form-group">
              <label for="exampleInputEmail1">Cantidad de estudiantes</label>
              <input
                type="number"
                class="form-control"
                placeholder="Ingrese cantidad"
                v-model="capacity"
                required
              />
            </div>
          </form>
        </b-container>

        <template #modal-footer>
          <div class="w-100">
            <button
              type="button"
              @click="saveAddItem()"
              style="background-color: #1d425e; border: none; color: white"
              class="btn form-control my-2"
            >
              Agregar
            </button>
            <button
              type="button"
              @click="exit()"
              style="
                background-color: rgb(119, 35, 45);
                border: none;
                color: white;
              "
              class="btn form-control"
            >
              Cancelar
            </button>
          </div>
        </template>
      </b-modal>
    </div>
  </div>
</template>

<script>
import AlertError from "../../components/AlertError.vue";
export default {
  name: "TurnsRegister",
  components: { AlertError },
  props: {
    id: {
      type: String,
      default: null,
    },
  },
  data() {
    return {
      capacity: null,
      show: false,
      data: {
        idTwo: null,
        idOne: null,
      },
      turnos: [],
    };
  },
  methods: {
    exit() {
      this.show = false;
    },
    add() {
      this.listTurns();
      this.capacity = null;
      (this.data = {
        idTwo: null,
        idOne: parseInt(this.id),
      }),
        (this.show = true);
    },
    async listTurns() {
      await this.$axios
        .get(
          "/configuration/program/program-group/group-register?programId=" +
            this.id,
          {
            headers: {
              Authorization: `Bearer ${localStorage.token}`,
            },
          }
        )
        .then((res) => {
          this.turnos = res.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    async saveAddItem() {
      await this.$axios
        .post(
          "/configuration/program/program-group/group-register?capacity=" +
            this.capacity,
          this.data,
          {
            headers: {
              Authorization: `Bearer ${localStorage.token}`,
            },
          }
        )
        .then((res) => {
          this.show = false;
          this.$message.success(res.data.content);
          this.$parent.initialize();
        })
        .catch((e) => {
          console.log(e);
          this.show = false;
          this.$refs.error.showAlert(e.response.data.content);
        });
    },
  },
};
</script>
<style>
.modal-content {
  background-color: rgb(243, 243, 243);
}
</style>
